import React from 'react';
import { Grid, GridColumn, Icon } from 'semantic-ui-react'
import SideBar from '../../components/SideBar/SideBar';


class Projects extends React.Component {
    render() {
        return (
            <div className="App">

                <Grid vertical>
                    <GridColumn width={5}>
                        <SideBar/>
                    </GridColumn>
                    <GridColumn width={11}>
                        <Icon name='lightbulb outline' size='huge' />
                        <h2>Página de Projetos </h2>
                        <main>
                            <p>This section contains information projects...</p>
                        </main>
                    </GridColumn>
                </Grid>

            </div>

        );
    }

}

export default Projects;